
import '../terms&privacy.css';
import { Navbar,Container,Nav} from 'react-bootstrap';
//import {a} from 'react-bootstrap';
import logo from '../image/log.png';
import React from 'react';
function Header()
{
    const exit_game=()=>{
        window.ImiApp.postMessage("exit");
    }
    return(
        <header className="appbar">
        <Navbar  collapseOnSelect expand="lg" bg="dark" variant="dark" >
         <Container >
            <Navbar.Brand className="imitext" >
                  <img
                    alt=""
                    src={logo}
                    width="30"
                    height="30"
                    className="d-inline-block align-top"
                  />{' '}
                 <span style={{fontSize:"19px",fontWeight:"bolder",textAlign:"center"}}>IMI Games</span> 
              </Navbar.Brand>
                 <Nav className="ms-auto justify-content-end">
                    <Navbar.Brand >
                        <button  onClick={exit_game}  className="exitbutton">
                         X
                        </button>
                    </Navbar.Brand>
                  </Nav>
            </Container>
        </Navbar>
        </header>
           
    );
}

export default Header;